.inputCalculator {
    width: 8rem;
    height: 2rem;
    font-family: Roboto;
    font-size: 13px;
    border-radius: 5px;
    margin-top: 7px;
}

.buttonBack {
    margin: 9px;
    width: 9rem;
    font-family: cursive;
    font-size: 15px;
    border-radius: 5px;
}




.calkButInp,
.inputCalculator {
    margin: 7px;
    width: 4rem;
    height: 4rem;
    font-family: 'Roboto';
    font-size: 42px;
    border-radius: 9px;
}

@media only screen and (max-width: 500px) {
    .buttonBack {
        margin: 7px;
        width: 20rem;
        height: 2rem;
        font-family: Roboto;
        font-size: 13px;
        border-radius: 5px;

    }

    label {
        font-size: 33px;
        font-family: Roboto;
    }

    button,
    .inputCalculator {
        font-size: 33px;
        width: 5.5rem;
        height: 3.5rem;
        font-family: Roboto;
    }
}