* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    text-align: center;
    color: black
}

.btnClient {
    height: 3rem;
    margin: 7px;
    width: 8.5rem;
    height: 5.5rem;
    font-family: 'Roboto';
    font-size: 17px;
    border-radius: 5px;
}

.buttonBack {
    position: absolute;
    margin-top: 143px;
    height: 2rem;
    width: 20%;
    font-family: 'Roboto';
    font-size: 19px;
    left: 40%;
    border-radius: 5px;
    display: block;

}

.tBodyModal {
    position: absolute;
    margin-left: auto;
    left: 10%;
    top: 10%;
    height: 40%;
    width: 80%;
    margin-right: auto;
}

.buttonBackModal {
    color: black;
    font-size: 22px;
    position: absolute;
    left: 20px;
    width: 16%;
    left: 42%;
    bottom: 5px;
    background-color: #9ce059;


}

.logos {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-content: stretch;
    justify-content: center;
    align-items: stretch;
    justify-items: center;
}

.logo {
    width: 40%;
}

.ButtonActivPageNotMain {
    display: none;
}

a {
    color: blue;
}

.trModule {
    color: black;
}


.contener {
    margin-top: 25px;
    display: grid;
    flex-wrap: nowrap;
    flex-direction: column;
    align-items: stretch;
    grid-template-columns: repeat(5, 154px);
    grid-column: rever;
    justify-items: center;
    justify-content: center;
    align-content: stretch;

}


.modalTable {
    height: 40%;
    display: none;
    background-color: #888b8b;
    text-align: center;
    position: fixed;
    left: 5%;
    z-index: 111;
    opacity: .96;
    width: 90%;
    border-radius: 15px;
    top: 15%;
}



.trModule {
    font-size: 34px;
}



@media only screen and (max-width: 750px) {
    .buttonBack {
        margin-top: 218px;
        width: 30%;
        /* height: 2rem; */
        left: 35%;
        font-family: 'Roboto';
        border-radius: 5px;
    }

    .contener {
        margin-top: 25px;
        display: grid;
        flex-wrap: nowrap;
        flex-direction: column;
        align-items: stretch;
        grid-template-columns: repeat(4, 25%);
        grid-column: rever;
        justify-items: center;
        justify-content: center;
        align-content: stretch;
    }

    .buttonBackModal {
        font-size: 22px;
        color: black;
        position: absolute;
        left: 35%;
        /* height: 18%; */
        width: 30%;
        bottom: 5px;
        background-color: #9ce059;

    }
}



@media only screen and (max-width: 608px) {
    .buttonBack {
        position: static;
        margin-top: 38px;
        height: 2rem;
        width: 20rem;
        font-family: 'Roboto';
        font-size: 19px;
        left: 40%;
        border-radius: 5px;
        display: block;
    }

    .modalContener {
        height: 40%;
        display: none;
        background-color: #888b8b;
        text-align: center;
        position: fixed;
        left: 15%;
        z-index: 111;
        opacity: .8;
        width: 70%;
        border-radius: 15px;
        top: 15%;
    }


}

@media only screen and (max-width: 609px) {
    .tBodyModal {
        position: absolute;
        margin-left: auto;
        left: 1%;
        right: 1%;
        top: 10%;
        height: 40%;
        width: 98%;
        margin-right: auto;
    }

    .HeaderTable {
        border: none;
        height: 8px;
        background-color: #99a19c;
        color: black;
        font-family: 'Roboto';
        font-size: 1rem;
    }

    /* 
*/
    .btnClient {
        margin: 7px;
        width: 20rem;
        height: 2rem;
        font-family: revert;
        font-size: 13px;
        border-radius: 5px;
    }

    td {
        font-size: 18px;
    }

    .logo {
        max-width: 89%;
    }

    .contener {
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
        align-items: center;
        justify-content: center;

    }


}


