@font-face {
  font-family: 'Roboto';
  src: url(./data/Roboto/Roboto-Regular.ttf);
}

.App {

  font-family: Roboto;

  text-align: center;
}

button :hover {
  background-color: chartreuse;
}

html {
  background: url("./pictures/avif/europe-map.avif") no-repeat, center;
  background-size: cover;
  background-color: #f8f8f8;
}

@media only screen and (max-width: 335px) {
  .App {
    width: max-content;
  }

  html {
    background: url("./pictures/avif/europe-map.avif") no-repeat, center;
    background-size: cover;
    background-color: #f8f8f8;
  }
}