.HeaderTable {

    background-color: #d1dede;
    height: 25px;
    width: 90%;
    border: none;
    font-size: 19px;
    font-family: 'Roboto';
    color: #020202;
    /* left: 2%; */
    right: 4%;

}

.classNameTr {
    background-color: #e8f1f1;
    height: 25px;
    width: 90%;
    font-size: 19px;
    font-family: 'Roboto';
    color: #020202;
    /* left: 2%; */

}



@media only screen and (max-width: 750px) {
    .HeaderTable {
        position: sticky;
        /* height: 25px; */
        width: 68%;
        right: 4%;
        background-color: #8d9090;
        /* height: 25px; */
        /* width: 77px; */
        border: none;
        font-size: 21px;
        font-family: 'Roboto';
        color: #020202;
        right: 0%;

    }

    .td {

        background-color: #e8f1f1;
        position: relative;
        height: 25px;
        width: 90%;


        font-size: 3vw;
        font-family: 'Roboto';
        color: #020202;
        left: 4%;

    }

}

@media only screen and (max-width: 550px) {
    .classNameTr {
        background-color: #e8f1f1;
        height: 25px;
        width: 90%;
        font-size: 10px;
        /* font-family: 'Roboto'; */
        color: #020202;
    }

    .HeaderTable {
        position: static;
        height: 25px;
        width: 90%;
        right: 4%;
        background-color: #8d9090;
        /* height: 25px; */
        /* width: 77px; */
        border: none;
        font-size: 15px;
        font-family: 'Roboto';
        color: #020202;
    }

    .classNameTr .td {
        background-color: #e8f1f1;
        position: relative;
        height: 25px;
        width: 92%;
        /* right: 4%; */
        font-size: 3vw;
        font-family: 'Roboto';
        color: #020202;
        left: 3.5%;

    }

}