input {
    /* width: 65%; */

    margin: 7px;
    width: 20rem;
    height: 2rem;
    font-family: revert;
    font-size: 13px;
    border-radius: 5px;
}

button {
    margin: 7px;
    width: 20rem;
    height: 2rem;
    font-family: revert;
    font-size: 13px;
    border-radius: 5px;
}

p {
    margin-top: 10px;
    position: absolute;
    left: 25%;
    /* display: block; */
    width: 50%;
    /* border: 2px; */
    /* border: 2px solid #141313; */
    font-size: 33px;
}

@media only screen and (max-width: 770px) {
    p {
        margin-top: 10px;
        position: absolute;
        left: 10%;
        /* display: block; */
        width: 80%;
        /* border: 2px; */
        border: 2px solid #141313;
        font-size: 33px;
    }
}

